<template>
  <div id="app">
		<nav>
			<img src="@/assets/mandelivery.png" class="image">
		</nav>
		<div class="main-container">
			<el-container class="search-map-container">
				<el-row>
					<el-col class="jobs-container" :span="8">
						<el-row class="jobs-upper">
							<section class="data-section">
								<el-row class="item">
									<label for="Search">Add Job</label>
									<el-select
											v-model="newJob"
											filterable
											remote
											reserve-keyword
											placeholder="Add a location"
											:remote-method="remoteMethod"
											:loading="loading"
											@change="addJob"
									>
										<el-option
												v-for="item in options"
												:key="item.id"
												:label="item.title"
												:value="item">
										</el-option>
									</el-select>
								</el-row>

								<el-row class="item" v-if="jobs.length > 0">
									<label>Locations</label>
									<div class="item-locations">
										<el-tag
												v-for="(job, index) in jobs"
												type="info"
												:key="index"
												closable
												:disable-transitions="false"
												@close="handleClose(job)">
											{{job.title}}
										</el-tag>
									</div>
								</el-row>

							</section>
						</el-row>

						<el-row class="search-btn-container">
							<el-col>
								<el-button @click="calculateSequence" class="search-btn" type="primary" :loading="isOptimizing" :disabled="jobs.length < 1">Optimize route</el-button>
							</el-col>
						</el-row>
					</el-col>
					<el-col :span="16">
						<leaflet-map
								v-loading="isOptimizing"
								style="width: 100%;height: 600px;"
								ref="map"
								appId="qOoSeRi3XOkcgBElXgbM"
								appCode="4f5bPo1EIlGAGoYQA956lh2ol2YxMZ-s2FyjZnNYqGM"
								latitude="53.3227003"
								longitude="-6.2666459"
						/>
					</el-col>
				</el-row>
			</el-container>
			<footer class="footer">
				<span>Developed by<a href="" class="link"> @Joaquin</a></span>
			</footer>
		</div>
  </div>
</template>

<script>
import axios from 'axios'
import LeafletMap from "@/components/LeafletMap";

export default {
  name: 'App',

  components: {
		LeafletMap
  },

	data() {
		return {
			options: [],
			newJob: '',
			jobs: [],
			loading: false,
			isOptimizing: false,
			states: ["Alabama", "Alaska", "Arizona",
				"Arkansas", "California", "Colorado",
				"Connecticut", "Delaware", "Florida",
				"Georgia", "Hawaii", "Idaho", "Illinois",
				"Indiana", "Iowa", "Kansas", "Kentucky",
				"Louisiana", "Maine", "Maryland",
				"Massachusetts", "Michigan", "Minnesota",
				"Mississippi", "Missouri", "Montana",
				"Nebraska", "Nevada", "New Hampshire",
				"New Jersey", "New Mexico", "New York",
				"North Carolina", "North Dakota", "Ohio",
				"Oklahoma", "Oregon", "Pennsylvania",
				"Rhode Island", "South Carolina",
				"South Dakota", "Tennessee", "Texas",
				"Utah", "Vermont", "Virginia",
				"Washington", "West Virginia", "Wisconsin",
				"Wyoming"]
		}
	},

	async mounted() {
		this.jobs = []
	},

	methods: {
		async remoteMethod(query) {
			if (query !== '') {
				this.loading = true;
				var config = {
					method: 'get',
					url: `https://autocomplete.search.hereapi.com/v1/autocomplete?q=${query}&resultType=place&apiKey=4f5bPo1EIlGAGoYQA956lh2ol2YxMZ-s2FyjZnNYqGM`,
					headers: { }
				};
				this.options = await axios(config)
						.then(function (response) {
							return response.data.items
						})
						.catch(function (error) {
							console.log(error);
						});
				this.loading = false;
			} else {
				this.options = [];
			}
		},

		async addJob(job){
			if(!this.jobs.some(address => address.id === job.id)){
				job.position = await this.getJobLatLng(job.id)
				this.jobs.push(job)
				this.$refs.map.dropMarker(job.position, job.id)
			}else {
				this.$message({
					message: 'address has been included',
					type: 'error'
				})
			}
			this.newJob = ''
			this.options = []
		},

		getJobLatLng(id) {
			var config = {
				method: 'get',
				url: `https://lookup.search.hereapi.com/v1/lookup?id=${id}&apiKey=4f5bPo1EIlGAGoYQA956lh2ol2YxMZ-s2FyjZnNYqGM`,
				headers: { }
			};
			return axios(config)
					.then(function (response) {
						return response.data.position
					})
					.catch(function (error) {
						console.log(error);
					});
		},

		async calculateSequence(){
			this.isOptimizing = true
			let sequence = await this.$refs.map.calculateRouteSequence(this.jobs);
			this.$refs.map.updateMarkerLabel(sequence)
			this.$refs.map.drawRoute(sequence.map(e => {
				return {
					lng: e.location[0],
					lat: e.location[1]
				}
			}))
			this.isOptimizing = false
		},

		handleClose(job) {
			this.jobs.splice(this.jobs.indexOf(job), 1);
		},
	}
}
</script>

