<template>
	<div ref="map"></div>
</template>

<script>
import axios from "axios"

export default {
	name: "LeafletMap",

	data() {
		return {
			platform: {},
			map: {},
			sequenceMarkerGroup: {},
			routesGroup: {}
		}
	},

	props: {
		appId: String,
		appCode: String,
		latitude: String,
		longitude: String
	},

	created() {
		this.sequenceMarkerGroup = new window.H.map.Group();
		this.routesGroup = new window.H.map.Group();
		window.addEventListener('resize', () => this.$refs.map.getViewPort().resize());
	},

	async mounted() {
		const platform = new window.H.service.Platform({
			'apikey': '4f5bPo1EIlGAGoYQA956lh2ol2YxMZ-s2FyjZnNYqGM'
		});

		const layers = platform.createDefaultLayers();

		const map = new window.H.Map(
				this.$refs.map,
				layers.vector.normal.map,
				{
					center: {lat: this.latitude, lng: this.longitude},
					zoom: 10,
					pixelRatio: window.devicePixelRatio || 1
				},
		);

		this.map = map;

	},

	methods: {
		dropMarker(position, jobId) {
			var coords = {lat: position.lat, lng: position.lng}

			this.map.addObject(this.sequenceMarkerGroup);
			var marker = new window.H.map.Marker(coords, {data: {
				jobId: jobId
				}});
			this.sequenceMarkerGroup.addObject(marker)
		},

		async calculateRouteSequence(places) {
			let jobPlaces = {
				jobs: []
			}

			let vehicles = {
				vehicles: [
					{
						id: 1,
						profile: 'driving-car',
						start: [-6.2666459,53.3227003],
						capacity: [
							7
						],
						skills: [
							1
						]
					}
				]
			}

			let id = 1
			for (let job of places) {
				jobPlaces.jobs.push({
					id: id,
					jobId: job.id,
					amount: [1],
					location: [job.position.lng, job.position.lat],
					skills: [1]
				})
				id++
			}

			var data = JSON.stringify({
				...jobPlaces,
				...vehicles
			});

			var config = {
				method: 'post',
				url: 'https://api.openrouteservice.org/optimization',
				headers: {
					'Authorization': '5b3ce3597851110001cf62480b5be540b04342b8a81f444201f511e2',
					'Content-Type': 'application/json'
				},
				data : data
			};

			return axios(config).then(response => {
				let steps = response.data.routes[0].steps
				//console.log(jobPlaces.jobs)
				for(let step of steps) {
					if( jobPlaces.jobs.find(element => element.id === step.id)){
						//console.log(jobPlaces.jobs.find(element => element.id === step.id))
						step.id = jobPlaces.jobs.find(element => element.id === step.id).jobId
					}
				}
				return steps
			});
		},

		drawRoute(sequence) {
			this.map.addObject(this.routesGroup);

			this.map.getViewModel().setLookAtData({
				bounds: this.sequenceMarkerGroup.getBoundingBox()
			});

			let concatr = ''
			let origin = sequence.shift()
			let destination = sequence.pop()
			for(let i = 0; i < sequence.length; i++) {
				concatr = concatr + '&via=' + `${sequence[i].lat},${sequence[i].lng}`;
			}

			let url = `https://router.hereapi.com/v8/routes?origin=${origin.lat},${origin.lng}&transportMode=car&destination=${destination.lat},${destination.lng}${concatr}&return=polyline&apiKey=6LwhFuE0lctV7ZfW8uDOhvpmyZkUMYhd1axK-AOwz54`

			axios({
				"method": "GET",
				"url": url
			}).then(result => {
				this.routesGroup.removeAll()
				//console.log('oe', result.data.routes[0].sections)
				result.data.routes[0].sections.forEach(section => {
					let linestring = window.H.geo.LineString.fromFlexiblePolyline(section.polyline);
					let polyline = new window.H.map.Polyline(linestring, {
						style: {
							lineWidth: 4,
							strokeColor: 'rgba(0, 128, 255, 0.7)'
						}
					});
					//this.map.addObject(polyline);
					this.routesGroup.addObject(polyline)
					//this.map.setZoom(10);
/*					this.map.getViewModel().setLookAtData({
						bounds: polyline.getBoundingBox()
					});*/
				})

				console.log(this.map.getObjects())
			}, error => {
				console.error(error);
			});
		},

		updateMarkerLabel(jobs){
			let markers = this.sequenceMarkerGroup.getObjects()
			for(let marker of markers) {
				let index = jobs.findIndex(element => element.id === marker.data.jobId)
				var svgMarkup = '<svg  width="24" height="24" xmlns="http://www.w3.org/2000/svg">' +
						'<rect stroke="black" fill="${FILL}" x="1" y="1" width="22" height="22" />' +
						'<text x="12" y="18" font-size="12pt" font-family="Arial" font-weight="bold" ' +
						'text-anchor="middle" fill="${STROKE}" >' + index + '</text></svg>';

				var newIcon = new window.H.map.Icon(
						svgMarkup.replace('${FILL}', 'blue').replace('${STROKE}', 'red'))
				marker.setIcon(newIcon)
			}
		}
	}
}
</script>

